@mixin mobile{
    @media screen and (max-width:480px) {
        @content
    }
}
@mixin tablet{
    @media screen and (max-width:768px) {
        @content
    }
}
@mixin laptop{
    @media screen and (max-width:1200px) {
        @content
    }
}

*{
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    /* box-sizing: border-box; */
   
}



/* ***************** Home section ******** */


.home{
    
    background-color: #467d78;
    background-image: linear-gradient(to left, #43474c, #34363a, #252629, #171718, #252424);
    // background-image: url("https://www.transparenttextures.com/patterns/padded.png");
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: rgb(222, 212, 212);
}

.container{
    border: 1px solid white;
    border-radius: 10px;
    width: 100%;
    height: 100vh;
    display: flex;
    overflow: hidden;
}

/* **************** Sidebar section ********** */

.sidebar{
    flex: 1;
    background-color: white;
    // background-image: linear-gradient(to left, #4c5055, #34363a, #252629, #171718, #010101);
    overflow-y: auto;
    display: flex;
    flex-direction: column;
}

/* **************** Chat section ********** */


/* ************** Navbar Section ******** */
.navbar{
    display: flex;
    align-items: center;
    background-color: white;
    // background-image: linear-gradient(to left, #5a5e62, #404246, #333437, #222223, #010101);
    height: 3rem;
    /* justify-content: space-between; */
    // color: white;
    padding: 10px;
    overflow: hidden;
}

.navbar .logo{
    font-weight: bold; 
    @include tablet{
        display: none;
    }
}
.navbar .user
{
    display: flex;
    gap: 1rem;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    width: 100%;
    margin: 10px;
}

.navbar .user .userProfile
{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.userProfile p{
    margin: 5px 15px;
    align-items: center;
    font-size: 1em;
    font-weight: bold;
    color: black;
}
.navbar .user img{
    // background-color: #ddddf7;
    height: 2.5rem;
    width: 2.5rem;
    border-radius: 50%;
}

.navbar .user button{
    color: black;
    font-size: 0.8rem;
    border: none;
    align-items: center;

}

/* ***************SearchBar Section ***************** */

.search{
    // border-bottom: 1px solid gray;
    color: white;
    box-shadow: 2px 2px 2px white;
    display:flex;
    flex-direction: column;
}

.searchFrom{
    flex: 1;
    display: flex;
    margin: 1em;
}


.search input{

    padding: 0.4em;
    border-radius: 1em;
    font-size: 1.2em;
    padding-left: 0.8em;
    border: 1px solid gray;
    color: black;
    flex: 1;
    outline: none;
}

.search input:focus{
    border: 2px solid gray;
    outline: invert;
}

.search .userChat{
    border-bottom:  2px solid gray ;
}

.chats{
   overflow: auto;
    width: 100%;
}
.userChat{
    padding: 0.6em;
    display: flex;
    align-items: center;
    // gap: 10px;
    // color: white;
    cursor: pointer;
    border-bottom: 1px solid whitesmoke;
}
.userChat:hover{
    background-color: whitesmoke;
}

.userChat img{
    height: 3em;
    width: 3em;
    border-radius: 50%;
    object-fit: cover;
}

.userChatInfo{
  margin-left: 1em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: black;
}

