.profile_name{
    font-size: 1.1rem;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: black;
    padding-bottom: 0.5em;
    font-weight: bold;
}
.last-massege{
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
     color: rgb(144, 143, 141);
    font-size: 0.9rem;
}

/* ****************Chat  */

.chat{
    flex: 2;
    display: flex;
    flex-direction: column;
    
    
    .chatInfo{
        height: 50px;
        background-color: #5d5d8d;
        background-color: white;
        // background-image: linear-gradient(to left, #868b90, #5f6268, #63656a, #44444e, #222222);
        // background-color: #27496D;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;

        .profile{
            // background-color:#5d5d8d;
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: black;
            img{
                width: 3rem;
                height: 3rem;
                border-radius: 50%;
                margin: 5px;
                object-fit: cover;
                
            }
            span{
                margin-left: 1rem;
                font-size: 1.2rem;
                
            }
        }
    }
}

// .chat .chatInfo{
     
// }



.chat .chatInfo .chatIcons .img {
    margin: 1rem;
    cursor: pointer;
}

.userChat_Img{
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    margin: 5px;
    object-fit: cover;
}

/* *************Messages Part *********** */
.messages{
    // background-color: #ddddf7;
    // background-color: rgb(238, 236, 236);
     background-color: whitesmoke; 
    //  background-color: #cec9c9; 
    
    //  background-image: url("https://www.transparenttextures.com/patterns/cubes.png");
    // //  background-image: url("https://www.transparenttextures.com/patterns/cartographer.png"); 
    // background-image: url("https://www.transparenttextures.com/patterns/arches.png");
    padding: 10px;
    height: 100%;
    overflow: scroll;
    
}


/* *********************Message Section */
.message{
    display: flex;
    gap: 20px;
    margin-bottom: 1rem;


    &.owner{
        flex-direction: row-reverse;

        p{
            background-color: #9bb0f5;
            color: white;
            border-radius: 10px 0px 10px 10px;
            
        }
        a{
            color: white;
            border-radius: 10px 0px 10px 10px; 
            padding-right: 20px;
        }
        
      }
    
}
.messageInfo{
    display: flex;
    flex-direction: column;
    color: gray;
    font-weight: 300;

}
.messageInfo img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.message .messageContent{
    max-width: 70%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-end;
    
}
.messageContent img{
    width: 250px;
    
    
}
.messageImage:hover img{
   opacity:0.5;
    
}

.messageContent p{
        border-radius: 0px 10px 10px 10px;
        padding: 7px 43px 10px 10px;
        display: flex;
        background-color: rgb(194, 188, 188);
        color: rgb(18, 18, 18);
        // background-color: rgb(192, 190, 187); 
}

.messageContent a{
    color:black;
    font-size: 1rem;
    padding-right: 20px;
}

.messageImgIcon{
    // position:'absolute';
    // bottom: 0;
    // right: 0;
    margin: 10px;
    color:rgb(243, 244, 246);
    background-color: #707982;
    border: 1px solid rgb(143, 141, 141);
    border-radius: 50%;
}
.messageImage:hover .messageImgIcon{
   color:white;
   opacity : 1;
}






/* ***********Input Component ************* */

.inputMessage{
    // height: 50px;
    background-color: whitesmoke; 
    padding: 0.8em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: whitesmoke;
}

.inputMessage .send{
    display: flex;
    align-items: center;
    justify-content: space-between;

    input{
        border-radius: 50%;
        border:1px solid #5b4c4c;
        overflow-wrap: break-word;
    }
}
.chat-input-section{
    display: flex;
   flex: 1;

}
.inputMessage .send .icon{
    padding: 8px;
    /* gap: 10px; */
    color: black;
    cursor: pointer;

}
.inputMessage #text{
    background-color: rgb(255, 255, 255);
    width: 100%;
    height: 100%;
    color: black;

        padding: 0.4em;
        border-radius: 1em;
        font-size: 1.2em;
        padding-left: 0.8em;
        border: 1px solid gray;
        outline: none;
}

.inputMessage #text:focus{
outline: invert;
    border:2px solid gray;
}

