::-webkit-scrollbar {
    width: 5px;
    
  }

  ::-webkit-scrollbar-track {
    background: #f1f1f1;
    background-color: white;
    
  }
  ::-webkit-scrollbar-thumb {
    background: #888;
    
  }
  ::-webkit-scrollbar:horizontal {
    height: 0;
    width: 0;
    display: none;
  }
  
  ::-webkit-scrollbar-thumb:horizontal {
    display: none;
  }

  .icon{
    color: black;
  }

  @media  screen and (min-width:1500px) {
    .home{
        height: 100vh;
        overflow: hidden;
    }
    .container{
        max-width: 1500px;
        max-height: 1000px;
    }
  }



