
.formContainer{
    // background-color: #738ee8;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;

    background: #5c6bc0;
    width: 100%;
    background: -webkit-linear-gradient(to right, #b39ddb, #5c6bc0);
    box-shadow: 12px 12px 2px 1px rgb(30 30 73 / 20%); 
}

.formWrapper {
    z-index: 100;
    max-width: 400px;
    background-color: white;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 1rem;
    padding: 3rem;
    
}

.formWrapper .logo{
    color: #088395;
    font-weight: bold;
    font-size: 2rem;

}

.formWrapper .title{
    font-size: 1.2rem;
    color: #4B70F5;
}

.formWrapper form{
    
    display: flex;
    flex-direction: column;
    gap: 2rem;
    color:blank
    span{
        color: rgb(255, 255, 255);
    }
}

.formWrapper form input{
    padding: 0.5rem;
    border: none;
    border-bottom:2px solid #a7bcff;
    font-size: 1rem;
    // width: 100%;
    border-radius: 5px;
    color: black;
}
.formWrapper form button{
    background-color: #3f67e9;
    color: rgb(255, 255, 255);
    font-size: 1rem;
    padding: 0.5rem;
    border-radius: 15px;
    cursor: pointer;
    width: 100%;
}
.formWrapper{
    p{
        font-size: 1.1rem;
        color: rgb(114, 110, 110);
    
    }
    a{
        color: rgb(45, 25, 171);
    }

} 


.shapes {
    z-index: 0;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.shapes div {
    position: fixed;
    display: block;
    list-style: none;
    width: 1.25em;
    height: 1.25em;
    background: rgba(255, 255, 255, 0.2);
    animation: move 25s linear infinite;
    bottom: -150px;
}

.shapes div:nth-child(1) {
    left: 30%;
    height: 5em;
    width: 5em;
    animation-delay: 0s;
}

.shapes div:nth-child(2) {
    left: 15%;
    width: 1.25em;
    height: 1.25em;
    animation-delay: 2s;
    animation-duration: 12s;
}

.shapes div:nth-child(3) {
    left: 75%;
    width: 1.25em;
    height: 1.25em;
    animation-delay: 4s;
}

.shapes div:nth-child(4) {
    left: 45%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.shapes div:nth-child(5) {
    left: 70%;
    width: 1.25em;
    height: 1.25em;
    animation-delay: 0s;
}

.shapes div:nth-child(6) {
    left: 80%;
    width: 5em;
    height: 5em;
    animation-delay: 3s;
}

.shapes div:nth-child(7) {
    left: 40%;
    width: 7em;
    height: 7em;
    animation-delay: 7s;
}

.shapes div:nth-child(8) {
    left: 55%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.shapes div:nth-child(9) {
    left: 25%;
    height: 1em;
    width: 1em;
    animation-delay: 2s;
    animation-duration: 35s;
}

.shapes div:nth-child(10) {
    left: 90%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}

.background {
    background: #5c6bc0;
    width: 100%;
    height: 100vh;
    background: -webkit-linear-gradient(to right, #b39ddb, #5c6bc0);
}

@keyframes move {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100% {
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
}
